html, body {
  overflow-x: hidden;
  overflow-y: auto;
  max-width: 100vw;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #000000;
  color: #d1d5db;
  height: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 1.5rem;
  min-height: 100vh;
  background-color: #121212;
  color: #d1d5db;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 2rem 0;
  text-align: center;
  color: #6366f1;
  text-shadow: 0 0 5px rgba(99, 102, 241, 0.3);
}

.layout {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.sidebar {
  background: linear-gradient(145deg, #1f1f1f, #2a2a2a);
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(99, 102, 241, 0.3);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  box-sizing: border-box;
}

.sidebarTitle {
  font-size: 1.8rem;
  font-weight: 700;
  color: #818cf8;
  margin: 0 0 1.5rem 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.planList {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.planCard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #2a2a2a;
  border-radius: 0.75rem;
  padding: 1rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  overflow: hidden;
}

.planCard:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(99, 102, 241, 0.4);
}

.planCardContent {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-grow: 1;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 0.5rem;
  transition: background-color 0.3s ease;
}

.planCardContent:hover {
  background-color: #374151;
}

.planCardActive {
  background-color: #4f46e5;
  color: white;
}

.planCardActive:hover {
  background-color: #4338ca;
}

.planIcon {
  font-size: 1.5rem;
}

.planName {
  font-size: 1.1rem;
  font-weight: 600;
  flex-grow: 1;
}

.planDeleteButton {
  background-color: transparent;
  color: #ef4444;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  cursor: pointer;
  padding: 0.5rem;
  transition: color 0.3s ease;
  min-width: 2rem;
  min-height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.planDeleteButton:hover {
  color: #dc2626;
}

.noPlans {
  font-style: italic;
  color: #9ca3af;
  text-align: center;
  padding: 1rem;
}

.newPlanForm {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-color: #2a2a2a;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: inset 0 0 8px rgba(99, 102, 241, 0.2);
}

.input {
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: #1f1f1f;
  border: 1.5px solid #6366f1;
  color: #d1d5db;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s ease;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 8px rgba(129, 140, 248, 0.5);
  border-color: #818cf8;
}

.addPlanButton {
  background: linear-gradient(90deg, #4f46e5, #7c3aed);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.addPlanButton:hover {
  background: linear-gradient(90deg, #4338ca, #6d28d9);
  box-shadow: 0 0 15px rgba(129, 140, 248, 0.5);
}

.addPlanIcon {
  font-size: 1.2rem;
}

.buttonPrimary {
  background-color: #4f46e5;
  color: white;
  padding: 0.75rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.buttonPrimary:hover {
  background-color: #4338ca;
}

.buttonSecondary {
  background-color: #374151;
  color: #d1d5db;
  padding: 0.75rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.buttonSecondary:hover {
  background-color: #4b5563;
}

.mainPanel {
  background-color: #1f1f1f;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 0 15px rgba(99, 102, 241, 0.5);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  box-sizing: border-box;
}

.planTitle {
  font-size: 2rem;
  font-weight: 600;
  color: #818cf8;
  margin: 0 0 1rem 0;
  text-align: center;
}

.daySection {
  background-color: #2a2a2a;
  padding: 1rem;
  border-radius: 0.75rem;
  box-shadow: inset 0 0 8px rgba(99, 102, 241, 0.3);
  width: 100%;
  box-sizing: border-box;
  transition: box-shadow 0.2s ease, background-color 0.2s ease;
  cursor: pointer;
}

.daySection:active {
  box-shadow: inset 0 0 10px rgba(99, 102, 241, 0.5), 0 0 10px rgba(99, 102, 241, 0.3);
  background-color: #333333;
}

.dayTitle {
  font-size: 1.25rem;
  font-weight: 600;
  color: #818cf8;
  margin: 0 0 0.75rem 0;
  border-bottom: 1px solid #6366f1;
  padding-bottom: 0.3rem;
  text-align: center;
}

.tableWrapper {
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  touch-action: pan-x;
  scrollbar-width: thin;
  scrollbar-color: #6366f1 #2a2a2a;
}

.tableWrapper::-webkit-scrollbar {
  height: 10px;
}

.tableWrapper::-webkit-scrollbar-track {
  background: #2a2a2a;
  border-radius: 5px;
}

.tableWrapper::-webkit-scrollbar-thumb {
  background: #6366f1;
  border-radius: 5px;
}

.tableWrapper::-webkit-scrollbar-thumb:hover {
  background: #818cf8;
}

.exerciseTable {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
  border: 2px solid #6366f1;
  border-radius: 8px;
  overflow: hidden;
  min-width: 550px;
}

.exerciseTable th,
.exerciseTable td {
  padding: 0.75rem 0.5rem;
  text-align: center;
  border-right: 2px solid #6366f1;
  border-bottom: 2px solid #6366f1;
  vertical-align: middle;
}

.exerciseTable th:last-child,
.exerciseTable td:last-child {
  border-right: none;
}

.exerciseTable th {
  background-color: #1f1f1f;
  font-weight: 600;
  color: #818cf8;
}

.exerciseTable td {
  background-color: #2a2a2a;
}

.exerciseTable td:first-child {
  text-align: left;
  padding-left: 1rem;
}

.exerciseLink {
  color: #818cf8;
  text-decoration: none;
  font-weight: 600;
  white-space: nowrap;
}

.exerciseLink:hover {
  text-decoration: underline;
}

.actionsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 80px;
  height: 100%;
}

.deleteButton {
  background-color: #ef4444;
  color: white;
}

.editButton,
.deleteButton {
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
  font-size: 0.9rem;
  display: inline-block;
}

.editButton {
  background-color: transparent;
  color: #6366f1;
  font-weight: 600;
}

.editButton:hover {
  color: #818cf8;
}

.deleteButton:hover {
  background-color: #dc2626;
}

.addExerciseButton {
  margin-top: 1.5rem;
  width: 100%;
  padding: 0.75rem;
  background-color: #4f46e5;
  color: white;
  font-weight: 600;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(129, 140, 248, 0.5);
  position: relative;
  overflow: hidden;
}

.addExerciseButton:hover {
  background-color: #4338ca;
  box-shadow: 0 0 15px rgba(129, 140, 248, 0.7);
}

.addExerciseButton::before {
  content: "+";
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2rem;
}

.modalInput {
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: #2a2a2a;
  border: 1.5px solid #6366f1;
  color: #d1d5db;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s ease;
}

.modalInput:focus {
  outline: none;
  box-shadow: 0 0 8px rgba(129, 140, 248, 0.5);
  border-color: #818cf8;
}

.modalButtons {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.modalButtons button {
  flex: 1;
}

@keyframes buttonEntry {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.addExerciseButton {
  animation: buttonEntry 0.5s ease-out;
}

@keyframes cardEntry {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.planCard {
  animation: cardEntry 0.5s ease-out;
}

@media (max-width: 768px) {
  .sidebar {
    padding: 1.5rem;
    max-width: 100%;
  }

  .planCard {
    padding: 0.75rem;
  }

  .planName {
    font-size: 1rem;
  }

  .planIcon {
    font-size: 1.2rem;
  }

  .planDeleteButton {
    font-size: 1.4rem;
    padding: 0.5rem;
    min-width: 2.5rem;
    min-height: 2.5rem;
  }

  .exerciseTable th,
  .exerciseTable td {
    padding: 0.5rem !important;
    font-size: 0.8rem;
    min-width: 70px;
    white-space: normal;
  }

  .exerciseTable td:first-child {
    padding-left: 1rem !important;
  }

  .actionsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 32px;
    min-width: 60px;
    height: 100%;
  }

  .editButton,
  .deleteButton {
    padding: 0.5rem 1rem;
    transition: opacity 0.3s ease;
    display: inline-block;
    width: auto;
  }

  .editButton {
    background-color: transparent;
    color: #6366f1;
    font-weight: 600;
  }

  .editButton:hover {
    color: #818cf8;
  }

  .deleteButton {
    background-color: #ef4444;
    color: white;
  }

  .deleteButton:hover {
    background-color: #dc2626;
  }

  .deleteButton {
    opacity: 0;
    pointer-events: none;
    z-index: 1;
  }

  .editing .deleteButton {
    opacity: 1;
    pointer-events: auto;
    display: inline-block !important;
  }

  .editing .editButton {
    opacity: 0;
    pointer-events: none;
  }
}

@media (max-width: 480px) {
  .sidebarTitle {
    font-size: 1.5rem;
  }

  .planCard {
    padding: 0.5rem;
  }

  .planName {
    font-size: 0.9rem;
  }

  .planIcon {
    font-size: 1rem;
  }

  .planDeleteButton {
    font-size: 1.2rem;
    padding: 0.4rem;
    min-width: 2rem;
    min-height: 2rem;
  }

  .exerciseTable th,
  .exerciseTable td {
    font-size: 0.75rem;
    min-width: 60px;
  }
}